import Vuex from 'vuex'

//
//vue
import app from './modules/app'

export default {
  install(Vue) {
    //Vuex
    Vue.use(Vuex)

    //store
    this.store = new Vuex.Store({
      modules: {
        app,
      }
    })
  },
  store: null//storeVue
}



